import * as THREE from 'three';

import {
  v3,
} from './utils';

export const PALLETE = {
  basic: 0x1abc9c,
  faces: 0x05101e,
  accent: 0x3498db,
  trails: [
    0x1abc9c,
    0x2ecc71,
    0x3498db,
    0x9b59b6,
    0xf1c40f,
    0xecf0f1,
    0xe67e22],
};

export const CAMERA_SPEED = 0.01;

export const CAMERA_SPEED_MOBILE = 5;

export const FAKE_LOADING_TIME = 0.1; // seconds

export const IMPORT_SCALE = 0.1;

export const CONSTRAINTS = {
  angleX: 50,
  angleY: 50,
  moveY: 15,
  minY: 2,
};

export const PATHS = {
    models: '/static/models/',
};

export const POSITIONS = [
  [v3(-2.4, 1.6, -1.40), v3(-15.14, 78.55, 105.96), v3(-57.59, 42.19, 73.77)],
  [v3(-0.3, 0.9, -1.7), v3(-1.28, 0.89, 5.41), v3(0.56, 5.53, 20.24)], // camera, pointA, pointB
  [v3(4.4, 2, 2), v3(-23.85, 44.63, -64.09), v3(-66.61, 32.57, 33.73)],
  [v3(-2.4, 1.6, -1.40), v3(4.40, 344.95, 2.00), v3(5.59, 276.40, 177.65)],
];

const edgesMat = new THREE.LineBasicMaterial({
  color: PALLETE.basic,
  linewidth: 2,
});

const edgesMatAccent = new THREE.LineBasicMaterial({
  color: PALLETE.accent,
  blending: THREE.AdditiveBlending,
});

const wireframeMat = new THREE.MeshBasicMaterial({
  color: PALLETE.basic,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.2,
  wireframe: true,
  blending: THREE.AdditiveBlending,
});

const facesMat = new THREE.MeshBasicMaterial({
  color: PALLETE.faces,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.2,
  blending: THREE.AdditiveBlending,
});

const facesMatAccent = new THREE.MeshBasicMaterial({
  color: PALLETE.accent,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.9,
  blending: THREE.AdditiveBlending,
});

export const MATERIALS = {
    edgesMat,
    edgesMatAccent,
    wireframeMat,
    facesMat,
    facesMatAccent,
};



