import {
  TimelineLite,
  TweenMax,
  Power0,
} from 'gsap';

const DIMENSION = {
  originX: 660,
  originY: 2700,
};

const STEP_MAP = {
  4: 0,
  5: 1,
  6: 2,
};

const ANIMATION_ELEMENTS = {
  0: [
    'line_x5F_1', 'arc_x5F_1', 'bus_x5F_info', 'line_x5F_2', 'girl',
    'line_x5F_3_9_', 'bus', 'line_x5F_4_2_', 'arc_x5F_2', 'tram_x5F_info',
    'line_x5F_4_3_', 'reading_x5F_guy', 'line_x5F_5', 'tram', 'line_x5F_6'],
  1: [
    'line_x5F_7', 'line_x5F_8', 'bike', 'bike_x5F_paths_x5F_info', 'tree',
    'line_x5F_9', 'grass', 'dog', 'line_x5F_10', 'bike_x5F_stations',
    'bike_x5F_machine', 'line_x5F_11', 'bike_x5F_station', 'line_x5F_12',
    'bike_x5F_parking_x5F_info', 'bike_x5F_parking', 'line_x5F_13'],
  2: [
    'clock', 'railway_x5F_time_x5F_chart', 'town_x5F_hall_x5F_time_x5F_chart',
    'railwaystation_x5F_icon', 'dworzec_x5F_info', 'town_x5F_hall', 'town_x5F_hall_x5F_info'],
};

export default {
  props: ['currentStep'],
  data() {
    return {
      animationElements: {},
      animationStep: 0,
      lastAnimationStep: -1,
      isActive: false,
      dimension: {
        x: 0,
        y: 0,
        right: 0,
      },
    };
  },
  watch: {
    currentStep(val) {
      console.log('--- step:', STEP_MAP[val]);
      this.position(val);
    },
  },
  mounted() {
    console.log('-- infoWroclavia mounted');

    this.containerEl = this.$el;
    this.infographicEl = this.$refs.infographic;


    this.$nextTick(() => {
      this.getAnimationElements();
      this.resize();
      window.addEventListener('resize', this.resize, false);
    });
  },
  methods: {
    animate() {
      console.log('current animation step:', this.animationStep);

      const elements = this.animationElements[this.animationStep];
      const tl = new TimelineLite();

      elements.forEach((element) => {
        if (element.className.baseVal === 'st2') {
          const pathLength = element.getTotalLength();
          const pathSpeed = pathLength / 400;
          element.style.strokeDasharray = pathLength;
          element.style.strokeDashoffset = pathLength;

          tl.add(TweenMax.to(element, pathSpeed, {
            strokeDashoffset: 0,
            ease: Power0.easeNone,
          }));
        } else if (element.id === 'bike') {
          element.style.opacity = 1;

          const path = document.getElementById('bike_path');
          const pathLength = path.getTotalLength();

          const pBase = path.getPointAtLength(0);
          const tweenData = {
            p: 0,
            t: 0,
          };

          TweenMax.to(tweenData, 2.5, {
            delay: 0.35,
            ease: Power0.easeNone,
            p: 1,
            onUpdate() {
              const p0 = path.getPointAtLength(tweenData.t * pathLength);
              const p = path.getPointAtLength(tweenData.p * pathLength);
              const angle = Math.atan2(p.y - p0.y, p.x - p0.x) * (180 / Math.PI);
              element.setAttribute('transform', `translate(${p.x - pBase.x}, ${p.y - pBase.y}) rotate(${angle})`);
              tweenData.t = tweenData.p;
            },
          });

          // debugger;
        } else if (element.id === 'bike_x5F_parking') {
          tl.add(TweenMax.to(element, 0.1, {
            opacity: 1,
          }));

          const bikes = element.querySelectorAll('path');
          for (let i = bikes.length - 1; i >= 0; i--) {
            tl.add(TweenMax.to(bikes[i], 0.05, {
              className: 'st1',
            }));
          }
        } else if (element instanceof SVGGElement) {
          tl.add(TweenMax.to(element, 0.01, {
            opacity: 1,
          }));

          const parts = element.querySelectorAll('g');
          if (parts && parts.length) {
            const tlInner = new TimelineLite();

            for (let i = parts.length - 1; i >= 0; i--) {
              parts[i].style.opacity = 0;
              tlInner.add(TweenMax.to(parts[i], 0.2, {
                opacity: 1,
              }));
            }
          }
        } else {
          tl.add(TweenMax.to(element, 0.2, {
            opacity: 1,
          }));
        }
      });

      this.lastAnimationStep = this.animationStep;
    },
    position(step) {
      const n = STEP_MAP[step];

      if (Number.isInteger(n)) {
        this.isActive = true;
        this.infographicEl.style.top = `${-n * this.dimension.step}px`;
        this.animationStep = Math.max(this.animationStep, n);

        if (this.animationStep > this.lastAnimationStep) {
          this.animate();
        }
      } else {
        this.isActive = false;
      }
    },
    resize() {
      const CONTAINER_DIMENSION = {
        x: this.containerEl.offsetWidth,
        y: this.containerEl.offsetHeight,
      };

      this.dimension.step = CONTAINER_DIMENSION.y;
      this.dimension.y = CONTAINER_DIMENSION.y * 3;
      this.dimension.x = (this.dimension.y / DIMENSION.originY) * DIMENSION.originX;
      this.dimension.right = Math.max((CONTAINER_DIMENSION.x / 4) - (this.dimension.x / 2), 0);

      if (this.dimension.x > CONTAINER_DIMENSION.x) {
        this.dimension.x = CONTAINER_DIMENSION.x;
        this.dimension.y = (this.dimension.x / DIMENSION.originX) * DIMENSION.originY;
        this.dimension.step = this.dimension.y / 3;
        this.dimension.right = 0;
      }

      this.infographicEl.style.height = `${this.dimension.y}px`;
      this.infographicEl.style.width = `${this.dimension.x}px`;
      this.infographicEl.style.right = `${this.dimension.right}px`;

      this.position(this.currentStep);
    },
    getAnimationElements() {
      console.warn('--- getAnimationElements & cache');
      Object.keys(ANIMATION_ELEMENTS).forEach((step) => {
        this.animationElements[step] = [];
        ANIMATION_ELEMENTS[step].forEach((elementId) => {
          const element = document.getElementById(elementId);

          if (element.className.baseVal === 'st2') {
            element.style.strokeDasharray = 10000;
            element.style.strokeDashoffset = 10000;
          } else {
            element.style.opacity = 0;
          }

          this.animationElements[step].push(element);
        });
      });
    },
  },
};



