<template>
  <div class="Landing" :class="['slide-' + animationDetails.currentStep, { 
      'loaded': animationDetails.isLoaded, 
      'scrolled' : animationDetails.isScrolled, 
      'scrolling' : animationDetails.isScrolling }]" ref="landing">
    <div class="background" :class="{ isOff: !animationDetails.active}">
      <Three :isStarted="loaded" :isActive="animationDetails.active" :step="animationDetails.renderStep" :updateCb="updateCb" @loaded="onLoaded" @progress="onProgress"></Three>      
      <div class="background-over"></div>
      <div class="background-over-hard"></div>
      <div class="background-over-middle"></div>
    </div>
    <div class="background-map"></div>
    <div class="overlay">
       <InfoWroclavia :currentStep="animationDetails.currentStep"></InfoWroclavia> 
    </div>

    <section id="slide-0">
      <h1>Przeprowadzamy si.</h1>
      <h2>Ju jesieni przenosimy si bliej centrum miasta do biurowego kompleksu</h2>
    </section>

    <section id="slide-1">
      <h1>hello world - slide 2</h1>
      <div class="tag" id="tag01">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>PGS</strong>
          <p>Nasze dotychczasowe biuro.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>
    </section>

    <section id="slide-2">

      <div class="tag" id="tag02">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>1750</strong>
          <p>mkw. powierzchni biurowej.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>

      <div class="tag" id="tag03">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>4300</strong>
          <p>mkw. zielonych ogrodw w strefie relaksu.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>

      <div class="tag" id="tag04">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>PKP</strong>
          <p>blisko dworca zapewnia atwy dojazd.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>

      <div class="tag" id="tag05">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>3</strong>
          <p>stacje rowerw miejskich w pobliu.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>

      <div class="tag" id="tag06">
        <div class="tag-point"></div>
        <div class="tag-label"><strong>30+</strong>
          <p>restauracji oraz kawiarni w strefie dinning plaza.</p>
        </div>
        <svg class="tag-line">
          <line></line>
        </svg>
      </div>
    </section>

    <section id="slide-3">
      <h1>Jestemy skomunikowani!</h1>
      <h2>Centrum Wrocawia. Dojazd bezproblemowy.</h2>
    </section>

    <div class="mouse">
      <div class="scroll-wheel"></div>
    </div>

    <div class="scroll">
      <p>keep scrolling...</p>
    </div>

    <div class="loader">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <p>{{ progress }} %</p>
    </div>

    <!--<div class="Menu" :class="{ isActive: animationDetails.currentStep > 0 }">
      <a class="logo"><span>PGS Software</span></a>
      <ul>
        <li>About</li>
        <li>Contact</li>
      </ul>
    </div>-->
  </div>
</template>

<script>
  /* eslint-disable */
  import {
    throttle,
  } from 'lodash';

  import Three from './Three';
  import InfoWroclavia from './InfoWroclavia';

  import {
    normalizeWheel,
    project,
    pointCircle,
    v3,
  } from './../common/utils';

  const scrollSettings = {
    sensivity: 9,
    duration: 600,
  };

  const touch = {
    x: 0,
    y: 0,
  };

  const animationDetails = {
    active: true,
    animatedSteps: [0, 1, 2, 3],
    animationMap: {
      0: 0,
      1: 1,
      2: 2,
      3: 3,
    },
    currentStep: 0,
    isLoaded: false,
    isScrolled: false,
    isScrolling: false,
    renderStep: 0,
    totalSlides: 7,
  };

  const positions = [{
      id: 'tag01',
      labelOffset: [-220, -60],
      vertex: v3(-0.9, 0.8, -2.3),
      projected: null,
    },
    {
      id: 'tag02',
      labelOffset: [-220, -60],
      vertex: v3(2.4, 2.3, 1.9),
      projected: null,
    },
    {
      id: 'tag03',
      labelOffset: [-220, -60],
      vertex: v3(-3.5, 1.4, 4.9), // Wroclavia tarasy
      projected: null,
    },
    {
      id: 'tag04',
      labelOffset: [-120, 40],
      vertex: v3(14, 0.3, 1.1), // PKP
      projected: null,
    },
    {
      id: 'tag05',
      labelOffset: [-120, 40],
      vertex: v3(25.5, 0, 3.4), // Rower miejski
      projected: null,

    },
    {
      id: 'tag06',
      labelOffset: [-120, 40],
      vertex: v3(-3.0, 1, 13.1), // Strefa Dinning plaza
      projected: null,
    }
  ];

  let ticking = false;

  export default {
    name: 'landing',
    components: {
      InfoWroclavia,
      Three,
    },
    data() {
      return {
        animationDetails,
        progress: 0,
        loaded: false,
      };
    },
    methods: {
      onLoaded() {
        animationDetails.isLoaded = true;
      },
      onProgress(p) {
        this.progress = Math.ceil(p * 100);
      },
      updateCb(camera, renderer, elapsed) {
        positions.forEach((pos, idx) => {
          pos.projected = project(pos.vertex, camera, renderer, true);

          const p1 = {
            x: pos.projected.x + (Math.cos((elapsed * 5) + (idx * 2)) * 2),
            y: pos.projected.y + (Math.sin((elapsed * 5) + (idx * 2)) * 2),
          };

          const p2 = {
            x: pos.projected.x + (pos.labelEl.offsetWidth * 1) * (p1.x < (renderer.context.canvas.width / 2) ? 1 :
              -1),
            y: pos.projected.y + (renderer.context.canvas.height / 10) * (p1.y < (renderer.context.canvas.height /
              2) ? 1 : -1)
          };

          // update labels and lines
          pos.posEl.style.left = `${Math.floor(p1.x) - 15}px`;
          pos.posEl.style.top = `${Math.floor(p1.y) - 15}px`;

          const angle = Math.atan2(p2.y - p1.y, p2.x - p1.x);
          const p3 = pointCircle(90, angle, p2);

          pos.labelEl.style.left = `${p2.x - pos.labelEl.offsetWidth/2}px`;
          pos.labelEl.style.top = `${p2.y - pos.labelEl.offsetHeight/2}px`;
          pos.posEl.className = pos.projected.clamped ? 'tag-point tag-point--clamp' : 'tag-point';

          pos.lineEl.setAttribute('x1', p3.x);
          pos.lineEl.setAttribute('y1', p3.y);
          pos.lineEl.setAttribute('x2', Math.floor(p1.x));
          pos.lineEl.setAttribute('y2', Math.floor(p1.y));
        });
      },
    },
    mounted() {
      const wheelEventName = 'onwheel' in document.createElement('div') ? 'wheel' : // Modern browsers support "wheel"
        document.onmousewheel !== undefined ? 'mousewheel' : // Webkit and IE support at least "mousewheel"
        'DOMMouseScrol'; // let's assume that remaining browsers are older Firefox

      window.addEventListener(wheelEventName, throttle(fullPageScroll, 60), false);
      window.addEventListener('touchstart', throttle(onTouchStart, 60), false);
      window.addEventListener('touchend', throttle(onTouchEnd, 60), false);

      positions.forEach((pos) => {
        pos.labelEl = document.querySelector(`#${pos.id} .tag-label`);
        pos.posEl = document.querySelector(`#${pos.id} .tag-point`);
        pos.lineEl = document.querySelector(`#${pos.id} .tag-line > line`);
      });
    },
  };

  function onTouchStart(e) {
    touch.x = e.touches[0].clientX;
    touch.y = e.touches[0].clientY;
  }

  function onTouchEnd(e) {
    const delta = {
      x: e.changedTouches[0].clientX - touch.x,
      y: e.changedTouches[0].clientY - touch.y,
    };

    const wheelDelta = Math.abs(delta.x) > Math.abs(delta.y) ? delta.x : delta.y;

    fullPageScroll({
      wheelDelta,
    });

    touch.x = 0;
    touch.y = 0;
  }

  function fullPageScroll(e) {
    const delta = normalizeWheel(e).pixelY;

    if (ticking !== true && animationDetails.isLoaded) {
      if (delta >= -scrollSettings.sensivity) { // nextItem
        ticking = true;
        if (animationDetails.currentStep !== animationDetails.totalSlides - 1) {
          animationDetails.currentStep++;
        }
        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }

      if (delta <= scrollSettings.sensivity) { // prevItem
        ticking = true;
        if (animationDetails.currentStep !== 0) {
          animationDetails.currentStep--;
        }

        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }
    }
  }

  function slideDurationTimeout(slideDuration) {
    setTimeout(() => {
      ticking = false;
    }, slideDuration);
  }

  function scrollItems() {
    animationDetails.isScrolling = true;
    setTimeout(() => {
      animationDetails.isScrolling = false; // @todo: add checking if on last step
    }, 3500);

    animationDetails.active = animationDetails.animatedSteps.reduce(
      (prev, n) => animationDetails.currentStep === n || prev, false);

    const mappedStep = animationDetails.animationMap[animationDetails.currentStep];

    if (mappedStep !== undefined) {
      animationDetails.renderStep = mappedStep;
    }

    if (!animationDetails.isScrolled) {
      animationDetails.isScrolled = true;
    }
  }

</script>

<style lang="scss" src="./Landing.scss" scoped></style>



