/* eslint-disable */

import * as THREE from 'three';
import {
  MeshLine,
  MeshLineMaterial,
} from '../lib/Meshline';

const defaultOptions = {
  length: 0.1,
  lineWidth: 20,
  resolution: 100,
  scale: 0.1,
};

export default class Trail {
  constructor(color = 0xffffff, path, camera, width, height, options) {
    this.options = Object.assign(defaultOptions, options);
    this.step = Math.floor((1 / this.options.resolution) * 1000) / 1000;

    this.resolution = new THREE.Vector2(width, height);
    this.path = parsePath(path, this.options.scale);

    this.material = createMaterial(color, this.options.lineWidth, this.resolution, camera);

    this.curve = new THREE.CatmullRomCurve3(this.path.vertices);
    this.line = createLine(this.curve, this.options.length, this.options.resolution);

    this.lineMesh = new THREE.Mesh(this.line.geometry, this.material);

    this.progress = this.options.length;
  }

  reset() {
    const geometry = getStartGeometry(this.curve, this.options.length, this.options.resolution);
    this.line.setGeometry(geometry, p => p);
    this.progress = this.options.length;
  }

  run() {
    this.progress = Math.floor((this.progress + this.step) * 1000) / 1000;
     if (this.progress > 1) {
      this.reset();
    } else {
      const newPoint = this.curve.getPoint(this.progress);
      this.line.advance(newPoint);
    }
  }
}

function parsePath(path, scale) {
  if (path instanceof THREE.Geometry) {
    return path;
  }

  const g = new THREE.Geometry();

  if (path.length % 3 === 0) {
    const n = Math.floor(path.length / 3);
    for (let i = 0; i < n; i++) {
      g.vertices.push(new THREE.Vector3(path[i * 3] * scale, path[(i * 3) + 1] * scale, path[(i * 3) + 2] * scale));
    }
  }

  return g;
}

function createMaterial(color, lineWidth, resolution) {
  return new MeshLineMaterial({
    color: new THREE.Color(color),
    opacity: 0.75,
    resolution,
    sizeAttenuation: false,
    lineWidth,
    near: 0.01,
    far: 1000,
    depthWrite: true,
    depthTest: true,
    alphaTest: 0,
    transparent: true,
    blending: THREE.AdditiveBlending,
  });
}

function createLine(curve, length = 1, resolution = 100) {
  const line = new MeshLine();
  const geometry = getStartGeometry(curve, length, resolution);

  line.setGeometry(geometry, p => p);
  return line;
}

function getStartGeometry(curve, length, resolution) {
  const geometry = new THREE.Geometry();

  const adjustedResolution = length / (length * resolution);

  for (let i = 0; i < length; i += adjustedResolution) {
    geometry.vertices.push(curve.getPoint(i));
  }

  return geometry;
}



